@font-face { font-family: 'Roboto'; src: url("../assets/fonts/Roboto/Roboto-BoldItalic-webfont.woff") format("woff"); font-weight: bold; font-style: italic; }

@font-face { font-family: 'Rubik'; src: url(".../assets/fonts/Rubik/Rubik-Regular.ttf") format("ttf"); font-weight: normal; font-style: normal; }

